import { useEffect, useState } from 'react';
import css  from './priceList.module.scss' ;
import PriceListTherapy from './priceListTherapy';
import PriceListOrthopedics from './priceListOrthopedics'
import PriceListSurgery from './priceListSurgery'
import PriceListAesthetic from './priceListAesthetic'
import PriceListOrthodontics from './priceListOrthodontics'
import PriceListPeriodontics from './priceListPeriodontics'
import PriceListChildren from './priceListChildren'
import PriceListImplantology from './priceListImplantology';

const PriceList = ({changePage}) => {
    useEffect(() => {
        document.title = "   |  -   -"
        changePage("Price")
    }, [])

    const [selectedCategory, setSelectedCategory] = useState('1');
    const [selectedNameCategory, setSelectedNameCategory] = useState("");

    const changeCategory = (value) => {
        setSelectedCategory(value);

        changeNameCategory(value);
    }

    const changeNameCategory = (item) => {
        if (item === '1') setSelectedNameCategory('');
        if (item === '2') setSelectedNameCategory('');
        if (item === '3') setSelectedNameCategory('');
        if (item === '4') setSelectedNameCategory('')
        if (item === '5') setSelectedNameCategory(' ');
        if (item === '6') setSelectedNameCategory('');
        if (item === '7') setSelectedNameCategory('');
        if (item === '8') setSelectedNameCategory(' ');
        console.log(selectedNameCategory)
    }


    return(
        <div className={css.price}>
            <div className={`container ${css.priceContainer}`}>
                <div className={css.priceTitleBox}>
                    <h1>  </h1>
                </div>
                <div className={css.priceSelectRow}>
                    <div className={css.priceSelectBox}>
                        <h1>{selectedNameCategory}</h1>
                        <select value={selectedCategory} className={css.priceSelectBoxSelector} onChange={event => changeCategory(event.target.value)}>
                            <option value="1"></option>
                            <option value="2"></option>
                            <option value="3"></option>
                            <option value="4"></option>
                            <option value="5"> </option>
                            <option value="6"></option>
                            <option value="7"></option>
                            <option value="8"> </option>
                        </select>
                    </div>
                </div>
                {selectedCategory === "1" ? <PriceListTherapy /> : null}
                {selectedCategory === "2" ? <PriceListOrthopedics /> : null}
                {selectedCategory === "3" ? <PriceListSurgery /> : null}
                {selectedCategory === "4" ? <PriceListImplantology /> : null}
                {selectedCategory === "5" ? <PriceListAesthetic /> : null}
                {selectedCategory === "6" ? <PriceListOrthodontics /> : null}
                {selectedCategory === "7" ? <PriceListPeriodontics /> : null}
                {selectedCategory === "8" ? <PriceListChildren /> : null}
            </div>
        </div>
    );
};

export default PriceList;