import css from './mobileMenu.module.scss'

const MobileMenu = ({openMobileMenu}) => {
    return(
        <div className={css.mobileMenuOverlay}>
            <div className={css.mobileMenu}>
                <div className={css.mobileMenuRow}><a href="/#/about" onClick={() => openMobileMenu()}> </a></div>
                <div className={css.mobileMenuRow}><a href="/#/Specialists" onClick={() => openMobileMenu()}> </a></div>
                <div className={css.mobileMenuRow}><a href="/#/pricelist" onClick={() => openMobileMenu()}>  </a></div>
                {/* <div className={css.mobileMenuRow}><a href="/discounts"></a></div> */}
                <div className={css.mobileMenuRow}><a href="/#/reviews" onClick={() => openMobileMenu()}></a></div>
                <div className={css.mobileMenuRow}><a href="/#/contacts" onClick={() => openMobileMenu()}></a></div>
            </div>
        </div>
        
    );

}

export default MobileMenu